import { pluginName } from './constants';

const { CKEDITOR } = window;

/*
 *  CKEditor 4 colourâ€‘apply / remove plugin
 *  --------------------------------------
 *  â€¢ Apply colour:   wrap selection in <span style='color:â€¦'>,
 *                    then lift span so it wraps any inline tags.
 *  â€¢ Remove colour:  singleâ€‘pass removal that handles fullyâ€‘ or
 *                    partiallyâ€‘selected colour spans while keeping
 *                    every other inline style intact.
 */
CKEDITOR.plugins.add(pluginName, {
  requires: 'richcombo',
  init(editor) {
    const colorMap = CKEDITOR.tools.get_plugin_config(pluginName, editor);

    /* --------------------------- utils --------------------------- */
    const utils = {
      clone(el) {
        const copy = new CKEDITOR.dom.element(el.getName());
        Object.entries(el.getAttributes()).forEach(([k, v]) => copy.setAttribute(k, v));
        return copy;
      },
      wrapInside(node, chain) {
        return chain.reduceRight((inner, outer) => {
          const wrap = utils.clone(outer);
          wrap.append(inner);
          return wrap;
        }, node);
      },
      splitText(node, range) {
        const s = node.equals(range.startContainer) ? range.startOffset : 0;
        const e = node.equals(range.endContainer)   ? range.endOffset   : node.getLength();
        return [node.substring(0, s), node.substring(s, e), node.substring(e)];
      },
      wrapWithAncestorsInside(node, parentChain) {
        return parentChain.reduceRight((inner, outer) => {
          const clone = new CKEDITOR.dom.element(outer.getName());
          const attrs = outer.getAttributes();
          for (let k in attrs) clone.setAttribute(k, attrs[k]);
          clone.append(inner);
          return clone;
        }, node);
      }
    };

    /* -------------------- spanâ€‘lifter (apply) -------------------- */
    function liftColorSpans(range) {
      const walkRange = range.clone();
      walkRange.enlarge(CKEDITOR.ENLARGE_INLINE);
      const walker = new CKEDITOR.dom.walker(walkRange);
      walker.evaluator = n => n?.type === CKEDITOR.NODE_ELEMENT && n.getName() === 'span' && n.getStyle('color');
      let span;
      while ((span = walker.next())) {
        const inlineParent = span.getParent();
        if (!inlineParent || inlineParent.getName() === 'span') continue;
        if (span.getFirst()?.type === CKEDITOR.NODE_ELEMENT && span.getFirst().getName() === inlineParent.getName()) continue;
        const clone = utils.clone(inlineParent);
        while (span.getFirst()) clone.append(span.getFirst().remove());
        span.append(clone);
      }
    }

    /* -------------------- improved colour remover ------------------------ */
    function smartRemoveColorFromPartial(range) {
      console.log('ðŸŽ¯ Starting smartRemoveColorFromPartial');
      
      // Find all color spans that intersect with the range
      const colorSpansToProcess = [];
      const walker = new CKEDITOR.dom.walker(range);
      
      // First, collect all text nodes in the range
      const textNodesInRange = [];
      walker.evaluator = function (node) {
        return node.type === CKEDITOR.NODE_TEXT;
      };
      
      let textNode;
      walker.reset();
      while ((textNode = walker.next())) {
        const colorSpan = textNode.getAscendant(function (el) {
          return el.getName && el.getName() === 'span' && el.getStyle('color');
        }, true);
        
        if (colorSpan) {
          textNodesInRange.push({ textNode, colorSpan });
        }
      }
      
      // Group text nodes by their color span
      const spanGroups = {};
      textNodesInRange.forEach(({ textNode, colorSpan }) => {
        const spanId = colorSpan.$.outerHTML; // Use outerHTML as unique identifier
        if (!spanGroups[spanId]) {
          spanGroups[spanId] = {
            span: colorSpan,
            textNodes: []
          };
        }
        spanGroups[spanId].textNodes.push(textNode);
      });
      
      // Process each color span
      Object.values(spanGroups).forEach(({ span: colorSpan, textNodes }) => {
        console.log(`ðŸ” Processing color span with ${textNodes.length} text nodes`);
        
        // Process each text node in this span
        textNodes.forEach(textNode => {
          const fullText = textNode.getText();
          const startOffset = textNode.equals(range.startContainer) ? range.startOffset : 0;
          const endOffset = textNode.equals(range.endContainer) ? range.endOffset : fullText.length;

          const before = fullText.slice(0, startOffset);
          const selected = fullText.slice(startOffset, endOffset);
          const after = fullText.slice(endOffset);

          console.log(`ðŸ“ Text: "${fullText}" -> Before: "${before}" | Selected: "${selected}" | After: "${after}"`);

          // Skip if no part of this text node is selected
          if (!selected) return;

          const beforeFrag = before ? new CKEDITOR.dom.text(before) : null;
          const selectedFrag = selected ? new CKEDITOR.dom.text(selected) : null;
          const afterFrag = after ? new CKEDITOR.dom.text(after) : null;

          // Get the chain of ancestors between textNode and colorSpan
          const parentChain = [];
          let current = textNode.getParent();
          while (current && !current.equals(colorSpan)) {
            parentChain.unshift(current);
            current = current.getParent();
          }

          // Create replacement fragments
          const fragments = [];

          if (beforeFrag) {
            const span = new CKEDITOR.dom.element('span');
            span.setAttribute('style', colorSpan.getAttribute('style'));
            const wrapped = utils.wrapWithAncestorsInside(beforeFrag, parentChain);
            span.append(wrapped);
            fragments.push(span);
            console.log('â¬…ï¸ Created left part with original color');
          }

          if (selectedFrag) {
            const originalStyle = colorSpan.getAttribute('style') || '';
            const keptStyle = originalStyle
              .split(';')
              .map(s => s.trim())
              .filter(s => s && !s.startsWith('color'))
              .join('; ');

            let wrapped = utils.wrapWithAncestorsInside(selectedFrag, parentChain);

            if (keptStyle) {
              const midSpan = new CKEDITOR.dom.element('span');
              midSpan.setAttribute('style', keptStyle);
              midSpan.append(wrapped);
              wrapped = midSpan;
            }

            fragments.push(wrapped);
            console.log(`âœ¨ Created middle part without color: "${keptStyle}"`);
          }

          if (afterFrag) {
            const span = new CKEDITOR.dom.element('span');
            span.setAttribute('style', colorSpan.getAttribute('style'));
            const wrapped = utils.wrapWithAncestorsInside(afterFrag, parentChain);
            span.append(wrapped);
            fragments.push(span);
            console.log('âž¡ï¸ Created right part with original color');
          }

          // Insert fragments before the text node
          const insertionPoint = textNode;
          fragments.forEach(fragment => {
            insertionPoint.insertBeforeMe(fragment);
          });

          // Remove the original text node
          textNode.remove();
        });
        
        // Clean up empty spans
        if (colorSpan.getChildCount() === 0) {
          colorSpan.remove();
          console.log('ðŸ§¹ Removed empty color span');
        } else {
          // Check if span only contains empty elements
          let hasContent = false;
          const childWalker = new CKEDITOR.dom.walker(new CKEDITOR.dom.range(colorSpan.getDocument()));
          childWalker.range.selectNodeContents(colorSpan);
          childWalker.evaluator = function(node) {
            if (node.type === CKEDITOR.NODE_TEXT && node.getText().trim()) {
              hasContent = true;
              return false; // Stop walking
            }
            return false;
          };
          childWalker.next();
          
          if (!hasContent) {
            colorSpan.remove();
            console.log('ðŸ§¹ Removed color span with no text content');
          }
        }
      });
    }

    /* ---------------------- UI combo ----------------------------- */
    editor.ui.addRichCombo('ApplyColor', {
      label: 'Apply Color',
      toolbar: 'styles',
      panel: {
        css: [CKEDITOR.skin.getPath('editor'), ...editor.config.contentsCss],
        multiSelect: false
      },
      init() {
        this.add('default', 'Remove Color', 'Remove Color');
        for (const [label, val] of Object.entries(colorMap)) this.add(val, label, label);
      },
      onRender() { this.setValue(''); },
      onClick(choice) {
        editor.focus();
        let sel = editor.getSelection();
        if (!sel) return;

        const ranges = sel.getRanges();
        editor.fire('lockSnapshot');

        if (choice === 'default') {
          ranges.forEach((range, index) => {
            console.log(`ðŸ“Œ Processing range ${index + 1}`);
            range.enlarge(CKEDITOR.ENLARGE_INLINE);
            smartRemoveColorFromPartial(range);
          });
        } else {
          editor.applyStyle(new CKEDITOR.style({ element: 'span', styles: { color: choice } }));
          sel = editor.getSelection();
          sel?.getRanges().forEach(liftColorSpans);
        }

        editor.fire('unlockSnapshot');
      }
    });
  }
});